
[data-v-322eccff]::-webkit-scrollbar
{
    width: 4px;
    background-color: red;
}

/*定义滚动条轨道 内阴影+圆角*/
[data-v-322eccff]::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
[data-v-322eccff]::-webkit-scrollbar-thumb
{
    border-radius: 2px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #36d860;
}
